import LoanStatusService from '@/services/loan/LoanStatusService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LoanStatusTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultLoanStatusTableFilter: {
      type: String,
      default: null
    },
    defaultLoanStatusTableOrderBy: {
      type: String,
      default: null
    },
    statusBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      loanStatusTableData: [],
      loanStatusTableFilterTimer: null,
      loanStatusTableFilter: this.defaultLoanStatusTableFilter,
      loanStatusTableOrderBy: this.defaultLoanStatusTableOrderBy,
      loanStatusTableContainer: 'loading-container',
      loanStatusTableBusy: false,
      loanStatusTableTotalRows: 0,
      loanStatusTableCurrentPage: 1,
      loanStatusTablePerPage: 10
    }
  },
  computed: {
    loanStatusTableNoDataText: function () {
      return this.loanStatusTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    loanStatusTableTotalPages: function () {
      return Math.ceil(this.loanStatusTableTotalRows / this.loanStatusTablePerPage)
    },
    loanStatusTablePagingInfo: function () {
      return this.pagingInfo(this.loanStatusTableCurrentPage, this.loanStatusTablePerPage, this.loanStatusTableTotalRows)
    }
  },
  methods: {
    loadLoanStatusTableData (loadingContainerId) {
      let self = this
      this.loanStatusTableBusy = true
      this.loanStatusTableContainer = loadingContainerId || this.loanStatusTableContainer
      this.startLoading(this.loanStatusTableContainer)
      LoanStatusService.loanStatuses({
        pageIndex: this.loanStatusTableCurrentPage - 1,
        pageSize: this.loanStatusTablePerPage,
        filter: this.loanStatusTableFilter,
        orderByField: this.loanStatusTableOrderBy,
        statusBehaviourEnumId: this.statusBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.loanStatusTableContainer)
        self.loanStatusTableBusy = false
        self.loanStatusTableTotalRows = response.data.totalRows
        self.loanStatusTableData = response.data.loanStatusList || []
      }).catch(function (error) {
        self.stopLoading(self.loanStatusTableContainer)
        self.loanStatusTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLoanStatusTable () {
      this.loadLoanStatusTableData()
    },
    filterLoanStatusTable (filter) {
      let self = this
      if (this.loanStatusTableFilter !== filter) {
        clearTimeout(this.loanStatusTableFilterTimer)
        this.loanStatusTableFilterTimer = setTimeout(() => {
          self.loanStatusTableFilter = filter
          self.loadLoanStatusTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.loanStatusTableFilterTimer)
      }
    },
    orderLoanStatusTable (orderBy, active) {
      this.loanStatusTableOrderBy = (active ? '' : '-') + orderBy
      this.loadLoanStatusTableData()
    }
  },
  watch: {
    loanStatusTableCurrentPage: function () {
      this.refreshLoanStatusTable()
    },
    loanStatusTablePerPage: function () {
      this.refreshLoanStatusTable()
    },
    statusBehaviourEnumId: function () {
      this.refreshLoanStatusTable()
    }
  }
}

export default LoanStatusTableMixin
